const initComponent = (componentNode) => {
    const block = componentNode;
    const days = block.querySelectorAll('.jsScheduleDay');
    const inputFrom = block.querySelector('.jsScheduleInputFrom');
    const inputTo = block.querySelector('.jsScheduleInputTo');
    const inputsWrapper = inputTo.closest('.input__field-group');
    let tempDateTo = '';
    let tempDateFrom = '';
    let tempTime = '';

  
    if (!block) {
        return;
    }

    checkDays()

    for (let index = 0; index < days.length; index++) {
        const item = days[index];

        item.addEventListener('click', ()=>{
            item.classList.toggle('is-active')
            checkDays()
        })
    }
    
    inputFrom.addEventListener('input', ()=> {
        tempDateFrom = inputFrom.value;
        tempTime = tempDateFrom + ' - ' + tempDateTo;
        setTime()
    })
    
    inputTo.addEventListener('input', ()=> {
        tempDateTo = inputTo.value;
        tempTime = tempDateFrom + ' - ' + tempDateTo;
        setTime()
    })

    function checkDays() {
        let activeDays = block.querySelectorAll('.jsScheduleDay.is-active');

        if(activeDays.length > 0){
            inputsWrapper.removeAttribute('disabled');

            for (let index = 0; index < activeDays.length; index++) {
                const day = activeDays[index];
                const dayInput = day.querySelector('input');
                let thisValue = dayInput.value;
                let prevValue = index != 0 ? activeDays[index - 1].querySelector('input').value : thisValue;
    
                if(prevValue == thisValue){
                    tempTime = thisValue;
                } else {
                    tempTime = '';
                }
            }
        } else{
            tempTime = '';
            inputsWrapper.setAttribute('disabled', true);
        }

        setValue()
    }

    function setValue () {
        let splitTime = tempTime.split(' - ');

        tempDateFrom = splitTime[0] != undefined ? splitTime[0] : '';
        tempDateTo = splitTime[1] != undefined ? splitTime[1] : '';
        inputFrom.value = tempDateFrom;
        inputTo.value = tempDateTo;
    }

    function setTime () {
        let activeDays = block.querySelectorAll('.jsScheduleDay.is-active');

        for (let index = 0; index < activeDays.length; index++) {
            const day = activeDays[index];
            const dayInput = day.querySelector('input');
            dayInput.value = tempTime;
        }
    }
};
  
document.addEventListener('DOMContentLoaded', () => {
    const componentNodes = Array.from(document.querySelectorAll('.jsSchedulePicker'));

    componentNodes.forEach(initComponent);
});